import {openModal,closeModal} from './modal'

const toast = (_msg, _duration, _extraclass) => {

    // 函数对象式声明
    if (typeof _msg === 'object') {
        let { text = '', duration = 2000, extraclass = '', } = _msg
        _msg = text
        _duration = duration
        _extraclass = extraclass
    }
    // 表达式声明
    else if (typeof _duration != 'number'){
        _extraclass = _duration
        _duration = 2000
    }

    var $toast = $('<div class="modal toast ' + (_extraclass || '') + '">' + _msg + '</div>').appendTo(document.body);
    openModal($toast, function(){
        setTimeout(function() {
            closeModal($toast);
        }, _duration || 2000);
    });
    return $toast;
};
const closeToast = ($toast) => {
    closeModal($toast);
};
export {toast,closeToast}


